const router = createRouter({
  history: createWebHistory(),
  routes
});

function changeUrl(newUrl) {
  window.history.pushState({}, '', newUrl); // Aktualizacja URL w historii przeglądarki
  updateCanonicalUrl(newUrl); // Aktualizacja tagu canonical
}

function updateCanonicalUrl(url) {
  let canonicalLink = document.querySelector('link[rel="canonical"]');
  if (canonicalLink) {
      canonicalLink.setAttribute('href', url);
      console.log("1: " + canonicalLink);

  } else {
      canonicalLink = document.createElement('link');
      canonicalLink.setAttribute('rel', 'canonical');
      canonicalLink.setAttribute('href', url);
      document.head.appendChild(canonicalLink);
      console.log("2: " + canonicalLink);
  }
}

function updateTiteAndDescriptionAndKeywords(title, description, keywords) {
  if (title) 
    document.title = title;
  
  if (description) {
    let descriptionMeta = document.querySelector('meta[name="description"]');
    if (descriptionMeta) {
      descriptionMeta.setAttribute('content', description);
    } else {
      descriptionMeta = document.createElement('meta');
      descriptionMeta.setAttribute('name', 'description');
      descriptionMeta.setAttribute('content', description);
      document.head.appendChild(descriptionMeta);
    }
  }
  if (keywords) {
    let keywordsMeta = document.querySelector('meta[name="keywords"]');
    if (keywordsMeta) {
      keywordsMeta.setAttribute('content', keywords);
    } else {
      keywordsMeta = document.createElement('meta');
      keywordsMeta.setAttribute('name', 'keywords');
      keywordsMeta.setAttribute('content', keywords);
      document.head.appendChild(keywordsMeta);
      console.log(keywordsMeta);
    }
  }
}


router.beforeEach((to, from, next) => {
  const viewportMeta = document.querySelector('meta[name="viewport"]');
  if (viewportMeta) {
    viewportMeta.content = 'width=device-width, initial-scale=1.0';
  }
  next();
});

router.beforeEach((to, from, next) => {
  if (to.meta.canonicalUrl) 
    updateCanonicalUrl(to.meta.canonicalUrl);
  
  updateTiteAndDescriptionAndKeywords(to.meta.title, to.meta.description, to.meta.keywords);
 
  next();
});


export default router;