import { createApp } from 'vue';
import App from './App.vue';
import router from './router/router';


import('bootstrap-vue-next').then(BootstrapVueNext => {
    import('bootstrap/dist/css/bootstrap.css').then(() => {
      import('bootstrap-vue-next/dist/bootstrap-vue-next.css').then(() => {
        app.use(BootstrapVueNext.default);
      });
    });
  });
  
  // Dynamicznie importuj FontAwesome
  import('@fortawesome/fontawesome-free/css/all.css').then(() => {
    import('@fortawesome/fontawesome-free/js/all.js');
  });

import './styles.css';

const app = createApp(App);

app.use(router);

app.mount('#app');
