<template>
    <router-view />
</template>

<script>

export default {
  name: 'App',
  watch: {
    $route(to) {
      if (to.meta.title) {
        document.title = to.meta.title;
      }
      if (to.meta.description) {
        this.updateMeta('description', to.meta.description);
      }
      if (to.meta.keywords) {
        this.updateMeta('keywords', to.meta.keywords);
      }
      if (to.meta.canonicalUrl) {
        this.updateCanonicalUrl(to.meta.canonicalUrl);
      }
    }
  },
  mounted() {
    if (this.$route.meta.title) {
      document.title = this.$route.meta.title;
    }
    if (this.$route.meta.description) {
      this.updateMeta('description', this.$route.meta.description);
    }
    if (this.$route.meta.keywords) {
      this.updateMeta('keywords', this.$route.meta.keywords);
    }
    if (this.$route.meta.canonicalUrl) {
      this.updateCanonicalUrl(this.$route.meta.canonicalUrl);
    }
  },
  methods: {
    updateMeta(name, content) {
      let meta = document.querySelector(`meta[name="${name}"]`);
      if (meta) {
        meta.setAttribute('content', content);
      } else {
        meta = document.createElement('meta');
        meta.setAttribute('name', name);
        meta.setAttribute('content', content);
        document.head.appendChild(meta);
      }
    },
    updateCanonicalUrl(url) {
      let link = document.querySelector('link[rel="canonical"]');
      if (link) {
        link.setAttribute('href', url);
      } else {
        link = document.createElement('link');
        link.setAttribute('rel', 'canonical');
        link.setAttribute('href', url);
        document.head.appendChild(link);
      }
    }
  }
}
</script>

